"use client";

import React, { useState, useEffect } from 'react';
import { 
    BookOpen, 
    Clock, 
    Users, 
    Star, 
    Calendar, 
    MapPin, 
    Globe,
    Download,
    Play,
    FileText,
    ChevronRight,
    Heart,
    Share2,
    CheckCircle
} from 'lucide-react';
import Image from 'next/image';
import Link from 'next/link';
import { useSession } from 'next-auth/react';
import { useParams } from 'next/navigation';

interface CourseDetail {
    _id: string;
    code: string;
    name: string;
    description: string;
    credits: number;
    level: string;
    difficulty: 'beginner' | 'intermediate' | 'advanced';
    delivery: string[];
    language: string;
    prerequisites: string[];
    learningOutcomes: string[];
    assessmentMethods: string[];
    textbooks: Array<{
        title: string;
        author: string;
        isbn?: string;
        required: boolean;
    }>;
    school: {
        name: string;
        shortName: string;
        location: string;
    };
    programs: Array<{
        program: {
            name: string;
            code: string;
        };
        semester: number;
    }>;
    professors: Array<{
        name: string;
        email?: string;
        rating?: number;
    }>;
    stats: {
        enrollmentCount: number;
        rating: {
            average: number;
            count: number;
        };
        materialCount: number;
        averageGrade?: number;
        passRate?: number;
    };
    materials?: Array<{
        _id: string;
        title: string;
        category: string;
        type: string;
        week?: number;
        downloadUrl?: string;
    }>;
}

const CourseDetailPage = () => {
    const { data: session } = useSession();
    const params = useParams();
    const courseId = params?.id as string;
    
    const [course, setCourse] = useState<CourseDetail | null>(null);
    const [loading, setLoading] = useState(true);
    const [activeTab, setActiveTab] = useState('overview');
    const [isEnrolled, setIsEnrolled] = useState(false);
    const [enrolling, setEnrolling] = useState(false);

    // Mock course data
    const mockCourse: CourseDetail = {
        _id: courseId,
        code: 'CS101',
        name: 'Introduction to Computer Science',
        description: 'This comprehensive course introduces students to the fundamental concepts of computer science. Students will learn programming basics using Python, explore algorithms and data structures, and understand computational thinking. The course covers problem-solving techniques, software development principles, and prepares students for advanced computer science courses.',
        credits: 3,
        level: '1',
        difficulty: 'beginner',
        delivery: ['in-person', 'online'],
        language: 'english',
        prerequisites: ['High School Mathematics', 'Basic Computer Literacy'],
        learningOutcomes: [
            'Understand fundamental programming concepts and syntax',
            'Apply problem-solving strategies to computational problems',
            'Implement basic algorithms and data structures',
            'Develop simple software applications',
            'Demonstrate understanding of computational thinking'
        ],
        assessmentMethods: [
            'Programming Assignments (40%)',
            'Midterm Exam (25%)',
            'Final Project (25%)',
            'Class Participation (10%)'
        ],
        textbooks: [
            {
                title: 'Python Programming: An Introduction to Computer Science',
                author: 'John Zelle',
                isbn: '978-1590282755',
                required: true
            },
            {
                title: 'Think Python: How to Think Like a Computer Scientist',
                author: 'Allen B. Downey',
                required: false
            }
        ],
        school: {
            name: 'University of Toronto',
            shortName: 'UofT',
            location: 'Toronto, ON'
        },
        programs: [{
            program: {
                name: 'Computer Science',
                code: 'CS'
            },
            semester: 1
        }],
        professors: [{
            name: 'Dr. Sarah Johnson',
            email: 'sarah.johnson@utoronto.ca',
            rating: 4.5
        }],
        stats: {
            enrollmentCount: 245,
            rating: {
                average: 4.3,
                count: 89
            },
            materialCount: 12,
            averageGrade: 78.5,
            passRate: 92.3
        },
        materials: [
            {
                _id: '1',
                title: 'Course Introduction Slides',
                category: 'lecture-notes',
                type: 'pdf',
                week: 1
            },
            {
                _id: '2',
                title: 'Python Basics Tutorial',
                category: 'tutorials',
                type: 'video',
                week: 1
            },
            {
                _id: '3',
                title: 'Assignment 1: Hello World',
                category: 'assignments',
                type: 'pdf',
                week: 2
            }
        ]
    };

    useEffect(() => {
        const fetchCourse = async () => {
            setLoading(true);
            // Simulate API call
            setTimeout(() => {
                setCourse(mockCourse);
                setLoading(false);
            }, 1000);
        };

        if (courseId) {
            fetchCourse();
        }
    }, [courseId]);

    const handleEnroll = async () => {
        setEnrolling(true);
        // Simulate enrollment API call
        setTimeout(() => {
            setIsEnrolled(true);
            setEnrolling(false);
        }, 1500);
    };

    const getDifficultyColor = (difficulty: string) => {
        switch (difficulty) {
            case 'beginner': return 'bg-green-100 text-green-800';
            case 'intermediate': return 'bg-yellow-100 text-yellow-800';
            case 'advanced': return 'bg-red-100 text-red-800';
            default: return 'bg-gray-100 text-gray-800';
        }
    };

    const getCategoryIcon = (category: string) => {
        switch (category) {
            case 'lecture-notes': return <FileText className="w-4 h-4" />;
            case 'tutorials': return <Play className="w-4 h-4" />;
            case 'assignments': return <BookOpen className="w-4 h-4" />;
            default: return <FileText className="w-4 h-4" />;
        }
    };

    if (loading) {
        return (
            <div className="min-h-screen bg-gray-50 p-6">
                <div className="max-w-6xl mx-auto">
                    <div className="animate-pulse">
                        <div className="h-8 bg-gray-300 rounded w-3/4 mb-4"></div>
                        <div className="h-4 bg-gray-300 rounded w-1/2 mb-8"></div>
                        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                            <div className="lg:col-span-2">
                                <div className="bg-white rounded-lg shadow-md p-6">
                                    <div className="h-40 bg-gray-300 rounded mb-4"></div>
                                    <div className="h-4 bg-gray-300 rounded w-full mb-2"></div>
                                    <div className="h-4 bg-gray-300 rounded w-3/4"></div>
                                </div>
                            </div>
                            <div className="lg:col-span-1">
                                <div className="bg-white rounded-lg shadow-md p-6">
                                    <div className="h-10 bg-gray-300 rounded mb-4"></div>
                                    <div className="h-4 bg-gray-300 rounded w-full mb-2"></div>
                                    <div className="h-4 bg-gray-300 rounded w-2/3"></div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    if (!course) {
        return (
            <div className="min-h-screen bg-gray-50 flex items-center justify-center">
                <div className="text-center">
                    <BookOpen className="w-16 h-16 text-gray-300 mx-auto mb-4" />
                    <h2 className="text-2xl font-bold text-gray-900 mb-2">Course not found</h2>
                    <p className="text-gray-500 mb-4">The course you're looking for doesn't exist.</p>
                    <Link 
                        href="/courses" 
                        className="bg-blue-600 text-white px-4 py-2 rounded-lg hover:bg-blue-700 transition-colors"
                    >
                        Back to Courses
                    </Link>
                </div>
            </div>
        );
    }

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Header */}
            <div className="bg-white shadow-sm border-b">
                <div className="max-w-6xl mx-auto px-6 py-8">
                    <div className="flex justify-between items-start">
                        <div className="flex-1">
                            <div className="flex items-center gap-3 mb-2">
                                <span className="text-sm font-medium text-blue-600">{course.code}</span>
                                <span className={`px-2 py-1 rounded-full text-xs font-medium ${getDifficultyColor(course.difficulty)}`}>
                                    {course.difficulty}
                                </span>
                            </div>
                            <h1 className="text-3xl font-bold text-gray-900 mb-2">{course.name}</h1>
                            <p className="text-gray-600 mb-4">{course.school.name}</p>
                            
                            <div className="flex flex-wrap gap-4 text-sm text-gray-500">
                                <div className="flex items-center">
                                    <BookOpen className="w-4 h-4 mr-1" />
                                    {course.credits} Credits
                                </div>
                                <div className="flex items-center">
                                    <Users className="w-4 h-4 mr-1" />
                                    {course.stats.enrollmentCount} enrolled
                                </div>
                                <div className="flex items-center">
                                    <Star className="w-4 h-4 mr-1 fill-current text-yellow-400" />
                                    {course.stats.rating.average} ({course.stats.rating.count} reviews)
                                </div>
                                <div className="flex items-center">
                                    <MapPin className="w-4 h-4 mr-1" />
                                    Level {course.level}
                                </div>
                            </div>
                        </div>

                        <div className="flex gap-2">
                            <button className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">
                                <Heart className="w-5 h-5" />
                            </button>
                            <button className="p-2 border border-gray-300 rounded-lg hover:bg-gray-50 transition-colors">
                                <Share2 className="w-5 h-5" />
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div className="max-w-6xl mx-auto px-6 py-8">
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                    {/* Main Content */}
                    <div className="lg:col-span-2">
                        {/* Navigation Tabs */}
                        <div className="bg-white rounded-lg shadow-sm mb-6">
                            <div className="border-b border-gray-200">
                                <nav className="flex space-x-8 px-6">
                                    {[
                                        { id: 'overview', label: 'Overview' },
                                        { id: 'materials', label: 'Materials' },
                                        { id: 'reviews', label: 'Reviews' },
                                        { id: 'instructor', label: 'Instructor' }
                                    ].map((tab) => (
                                        <button
                                            key={tab.id}
                                            onClick={() => setActiveTab(tab.id)}
                                            className={`py-4 px-1 border-b-2 font-medium text-sm ${
                                                activeTab === tab.id
                                                    ? 'border-blue-500 text-blue-600'
                                                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                                            }`}
                                        >
                                            {tab.label}
                                        </button>
                                    ))}
                                </nav>
                            </div>

                            {/* Tab Content */}
                            <div className="p-6">
                                {activeTab === 'overview' && (
                                    <div className="space-y-6">
                                        <div>
                                            <h3 className="text-lg font-semibold mb-3">Course Description</h3>
                                            <p className="text-gray-600 leading-relaxed">{course.description}</p>
                                        </div>

                                        <div>
                                            <h3 className="text-lg font-semibold mb-3">Learning Outcomes</h3>
                                            <ul className="space-y-2">
                                                {course.learningOutcomes.map((outcome, index) => (
                                                    <li key={index} className="flex items-start">
                                                        <CheckCircle className="w-5 h-5 text-green-500 mr-2 mt-0.5 flex-shrink-0" />
                                                        <span className="text-gray-600">{outcome}</span>
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>

                                        <div>
                                            <h3 className="text-lg font-semibold mb-3">Prerequisites</h3>
                                            <ul className="flex flex-wrap gap-2">
                                                {course.prerequisites.map((prereq, index) => (
                                                    <li key={index} className="bg-gray-100 text-gray-700 px-3 py-1 rounded-full text-sm">
                                                        {prereq}
                                                    </li>
                                                ))}
                                            </ul>
                                        </div>

                                        <div>
                                            <h3 className="text-lg font-semibold mb-3">Assessment Methods</h3>
                                            <ul className="space-y-2">
                                                {course.assessmentMethods.map((method, index) => (
                                                    <li key={index} className="text-gray-600">{method}</li>
                                                ))}
                                            </ul>
                                        </div>

                                        <div>
                                            <h3 className="text-lg font-semibold mb-3">Required Textbooks</h3>
                                            <div className="space-y-3">
                                                {course.textbooks.map((book, index) => (
                                                    <div key={index} className="border border-gray-200 rounded-lg p-4">
                                                        <div className="flex justify-between items-start">
                                                            <div>
                                                                <h4 className="font-medium text-gray-900">{book.title}</h4>
                                                                <p className="text-gray-600">by {book.author}</p>
                                                                {book.isbn && (
                                                                    <p className="text-sm text-gray-500">ISBN: {book.isbn}</p>
                                                                )}
                                                            </div>
                                                            <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                                                                book.required 
                                                                    ? 'bg-red-100 text-red-800' 
                                                                    : 'bg-blue-100 text-blue-800'
                                                            }`}>
                                                                {book.required ? 'Required' : 'Recommended'}
                                                            </span>
                                                        </div>
                                                    </div>
                                                ))}
                                            </div>
                                        </div>
                                    </div>
                                )}

                                {activeTab === 'materials' && (
                                    <div className="space-y-4">
                                        <h3 className="text-lg font-semibold">Course Materials</h3>
                                        {course.materials && course.materials.length > 0 ? (
                                            <div className="space-y-3">
                                                {course.materials.map((material) => (
                                                    <div key={material._id} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg hover:bg-gray-50 transition-colors">
                                                        <div className="flex items-center gap-3">
                                                            {getCategoryIcon(material.category)}
                                                            <div>
                                                                <h4 className="font-medium text-gray-900">{material.title}</h4>
                                                                <p className="text-sm text-gray-500">
                                                                    {material.category} • {material.type}
                                                                    {material.week && ` • Week ${material.week}`}
                                                                </p>
                                                            </div>
                                                        </div>
                                                        <button className="p-2 text-gray-400 hover:text-gray-600 transition-colors">
                                                            <Download className="w-5 h-5" />
                                                        </button>
                                                    </div>
                                                ))}
                                            </div>
                                        ) : (
                                            <p className="text-gray-500">No materials available yet.</p>
                                        )}
                                    </div>
                                )}

                                {activeTab === 'reviews' && (
                                    <div className="space-y-4">
                                        <h3 className="text-lg font-semibold">Student Reviews</h3>
                                        <div className="bg-gray-50 rounded-lg p-6 text-center">
                                            <Star className="w-12 h-12 text-gray-300 mx-auto mb-2" />
                                            <p className="text-gray-500">Reviews coming soon</p>
                                        </div>
                                    </div>
                                )}

                                {activeTab === 'instructor' && (
                                    <div className="space-y-4">
                                        <h3 className="text-lg font-semibold">Instructor</h3>
                                        {course.professors.map((professor, index) => (
                                            <div key={index} className="flex items-start gap-4 p-4 border border-gray-200 rounded-lg">
                                                <div className="w-16 h-16 bg-gray-200 rounded-full flex items-center justify-center">
                                                    <Users className="w-8 h-8 text-gray-400" />
                                                </div>
                                                <div>
                                                    <h4 className="font-semibold text-gray-900">{professor.name}</h4>
                                                    {professor.email && (
                                                        <p className="text-gray-600">{professor.email}</p>
                                                    )}
                                                    {professor.rating && (
                                                        <div className="flex items-center mt-1">
                                                            <Star className="w-4 h-4 fill-current text-yellow-400 mr-1" />
                                                            <span className="text-sm text-gray-600">{professor.rating}/5</span>
                                                        </div>
                                                    )}
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>

                    {/* Sidebar */}
                    <div className="lg:col-span-1">
                        <div className="bg-white rounded-lg shadow-sm p-6 sticky top-6">
                            {/* Enrollment Section */}
                            <div className="mb-6">
                                {isEnrolled ? (
                                    <div className="bg-green-50 border border-green-200 rounded-lg p-4 text-center">
                                        <CheckCircle className="w-8 h-8 text-green-500 mx-auto mb-2" />
                                        <p className="text-green-800 font-medium">You're enrolled!</p>
                                        <Link 
                                            href={`/courses/${course._id}/dashboard`}
                                            className="inline-block mt-2 text-green-600 hover:text-green-700 text-sm font-medium"
                                        >
                                            Go to Course Dashboard →
                                        </Link>
                                    </div>
                                ) : (
                                    <button
                                        onClick={handleEnroll}
                                        disabled={enrolling}
                                        className="w-full bg-blue-600 text-white py-3 px-4 rounded-lg font-medium hover:bg-blue-700 transition-colors disabled:opacity-50 disabled:cursor-not-allowed"
                                    >
                                        {enrolling ? 'Enrolling...' : 'Enroll Now'}
                                    </button>
                                )}
                            </div>

                            {/* Course Info */}
                            <div className="space-y-4">
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Credits</span>
                                    <span className="font-medium">{course.credits}</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Level</span>
                                    <span className="font-medium">Level {course.level}</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Language</span>
                                    <span className="font-medium capitalize">{course.language}</span>
                                </div>
                                <div className="flex justify-between">
                                    <span className="text-gray-600">Delivery</span>
                                    <div className="flex gap-1">
                                        {course.delivery.map((method) => (
                                            <span
                                                key={method}
                                                className="px-2 py-1 bg-blue-100 text-blue-800 text-xs rounded-full"
                                            >
                                                {method}
                                            </span>
                                        ))}
                                    </div>
                                </div>
                                {course.stats.averageGrade && (
                                    <div className="flex justify-between">
                                        <span className="text-gray-600">Average Grade</span>
                                        <span className="font-medium">{course.stats.averageGrade}%</span>
                                    </div>
                                )}
                                {course.stats.passRate && (
                                    <div className="flex justify-between">
                                        <span className="text-gray-600">Pass Rate</span>
                                        <span className="font-medium">{course.stats.passRate}%</span>
                                    </div>
                                )}
                            </div>

                            {/* Program Info */}
                            <div className="mt-6 pt-6 border-t border-gray-200">
                                <h4 className="font-semibold text-gray-900 mb-3">Program Information</h4>
                                {course.programs.map((program, index) => (
                                    <div key={index} className="text-sm">
                                        <p className="font-medium">{program.program.name}</p>
                                        <p className="text-gray-600">Semester {program.semester}</p>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CourseDetailPage; 